@charset "UTF-8";

@font-face {
  font-family: "the-movies";
  src:url("fonts/the-movies.eot");
  src:url("fonts/the-movies.eot?#iefix") format("embedded-opentype"),
    url("fonts/the-movies.woff") format("woff"),
    url("fonts/the-movies.ttf") format("truetype"),
    url("fonts/the-movies.svg#the-movies") format("svg");
  font-weight: normal;
  font-style: normal;

}

[data-icon]:before {
  font-family: "the-movies" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "the-movies" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

